@use 'variables' as v;
@use 'sass:color';
@use 'mixins' as m;
@use 'placeholders';


.header {
  width: 100%;
  // color.adjust(v.$white-color, $alpha: -0.85)
  background-color: v.$blue-color;

  border: {
    bottom: {
      width: 1px;
      style: solid;
      color: v.$white-color;
    }
  }

  .nav {
    height: 100%;
    height: 50px;
    @include m.d-flex(start, center);


    &__title {
      height: 100%;
      width: 255px;

      border-right: 1px solid v.$white-color;
      @include m.d-flex(center, center);




      &-link {
        color: v.$white-color;
        font-size: 25px;
        font-weight: 500;


        span {
          font-weight: 700;
        }

        @extend %hover-black;

      }
    }

    &__links {
      flex-grow: 1;


      .list {
        @include m.d-flex(start, center, nowrap, row, min(100px, 5vw), 0);
        padding: 0 0 0 min(100px, 5vw);

        &__item {

          &-link {
            font-size: 22px;
            font-weight: 500;
            color: v.$white-color;
            @extend %hover-black;
          }


        }


      }
      @media (max-width: v.$media-lg) {
        .list {

        }
      }

    }

    .covid {
      height: 100%;
      @include m.d-flex(center, center);
      padding: 0 30px;
      border-right: 1px solid v.$white-color;
      border-left: 1px solid v.$white-color;
      

      &-link {
        
        font-size: 16px;
        font-weight: 700;
        color: v.$white-color;
        @extend %hover-black;

      }
    }
    .globe {
      &-link {
        &-icon{
          padding: 0 12px 0 30px;
          height: 27px;
          @extend %hover-black-icon;
        }
      }
    }
  }

}